
.App-header {
  background-color: #fa0e7c;
  height: auto;
  display: flex;
}

.header{
  text-align: left;
  color: white;
  margin-top: 10px;
  margin-bottom: 10px;
  margin-left: 10px;
  font-size: 20px;
  font-style: inherit;
}

.faded{
  color: grey;
  font-size: 25px;
}

@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Poppins', sans-serif;
}

.card {
  width: 90%;
  height: auto;
  margin: 15px;
  margin-left: 3.5%;
  padding: 30px;
  border-radius: 5px;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  transition: 0.3s;
}

.checkbox{
  width: 90%;
  margin-left: 6%;
}

.checkbox input{
  width: 15px;
  height: 15px;
}
/* On mouse-over, add a deeper shadow */
.card:hover {
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
}

/* Add some padding inside the card container */
.container {
  padding: 2px 16px;
}

.btn {
  display: inline-block;
  background: #fa0e7c;
  color: #fff;
  border: none;
  width: 90%;
  padding: 10px 20px;
  margin-left: 5%;
  border-radius: 5px;
  cursor: pointer;
  text-decoration: none;
  font-size: 20px;
  font-family: inherit;
}

.btn:focus {
  outline: none;
}

.btn:active {
  transform: scale(0.98);
}

.btn-block {
  display: block;
  width: 100%;
}

.add-form {
  margin-bottom: 20px;
}

.form-control {
  margin: 20px 0;
}

.card input {
  width: 100%;
  height: 40px;
  margin: 5px;
  font-size: 17px;
}

.form-control-check {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.form-control-check label {
  flex: 1;
}

.form-control-check input {
  flex: 2;
  height: 20px;
}

footer {
  margin-top: 30px;
  text-align: center;
}

/* The switch - the box around the slider */
.switch {
  position: relative;
  width: 60px;
  height: 34px;
  float: right;
}

.wrg-toggle {
  touch-action: pan-x;
  display: block;
  position: relative;
  cursor: pointer;
  background-color: transparent;
  border: 0;
  padding: 0;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
  -webkit-tap-highlight-color: transparent;
  float: right;
}

.wrg-toggle-input {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

.wrg-toggle-check, .wrg-toggle-uncheck {
  position: absolute;
  width: 10px;
  height: 10px;
  top: 0;
  bottom: 0;
  margin-top: auto;
  margin-bottom: auto;
  line-height: 0;
  opacity: 0;
  transition: opacity .25s ease;
}
.wrg-toggle-check {
  left: 8px;
}
.wrg-toggle-uncheck {
  opacity: 1;
  right: 10px;
}

.wrg-toggle-uncheck {
  align-items: center;
  display: flex;
  height: 10px;
  justify-content: center;
  position: relative;
  width: 10px;
  background-color: white;
}

.wrg-toggle-container{
  width: 50px;
  height: 24px;
  padding: 0;
  margin-top: 10px;
  border-radius: 30px;
  background-color: #fa0e7c;
  transition: all .2s ease;
}

.wrg-toggle-circle{
  transition: all .5s cubic-bezier(.23,1,.32,1) 0ms;
  position: absolute;
  top: 1px;
  left: 1px;
  width: 22px;
  height: 22px;
  margin-top: 10px;
  border: 1px solid black;
  border-radius: 50%;
  background-color: #fafafa;
  box-sizing: border-box;
  transition: all .25s ease;
}

.wrg-toggle--checked .wrg-toggle-check{
  opacity: 1;
}
.wrg-toggle--checked .wrg-toggle-uncheck {
  opacity: 0;
}
.wrg-toggle--checked .wrg-toggle-circle {
  left: 27px;
}

input {
  border-top-style: hidden;
  border-right-style: hidden;
  border-left-style: hidden;
  border-bottom-style: hidden;
  background-color: white;
}

.no-outline:focus {
  outline: none;
}

.outerDiv {
  background-color: white;
  margin: 0px auto;
  padding: 5px;
}
  
.leftDiv {
  color: #000;
  width: 80%;
  float: left;
}
  
.rightDiv {
  color: #000;
  width: 20%;
  float: right;
}
  